import React from 'react';
import{Button, Card} from 'react-bootstrap'
import {Icon} from '@iconify/react'
import githubIcon from '@iconify/icons-simple-icons/github';

const ProjectCard = (props) => {
    return (
        <Card className="col-12 col-md-6 col-lg-3 mx-3 my-3">
            <Card.Body>
                <Card.Title>{props.name}</Card.Title>
                <Card.Text>{props.description}</Card.Text> 
                <Card.Text>
                   <strong>Language/Framework:   </strong>
                   {props.languages}
                </Card.Text>
            </Card.Body>
            <div className="d-flex flex-row justify-content-around mb-3">

                {/* The button for the github link.*/}
                <Button variant="outline-primary" href={props.github}>
                    <Icon icon={githubIcon}/>
                </Button>

                {/* Happens only if there is an url to check the project.*/}
                {props.url !== '' && <Button variant="outline-success" href={props.url}>It's live</Button>}
            </div>
        </Card>
    );
};

export default ProjectCard;